<template>
  <swiper class="swiper" :autoplay="swiper_options.autoplay" :loop=true :allowSlideNext=true :pagination="true" :modules="modules" :slidesPerView="1">
    <swiper-slide v-for="(item,index) in banner" :key="index"><a :href="item.link"><img :src="item.image" alt=""></a></swiper-slide>
  </swiper>
</template>

<script>

import { Swiper, SwiperSlide } from "swiper/vue";
import SwiperCore, { Pagination, Autoplay } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
SwiperCore.use([Autoplay,Pagination])
export default {
  props:{
    banner:{
      type:Array,
      default(){
        return []
      }
    }
  },
  components:{
    Swiper,
    SwiperSlide,
  },
  data (){
    return {
      swiper_options:{
        autoplay: {
          disableOnInteraction: false, // 
          delay: 2000, //2
        },
        speed: 500, //
        loop: true,
        slidesPerView: "auto", //
        centeredSlides: true, //slide
        spaceBetween: 20,
        coverflowEffect: {
          // rotate: 0, //slide3dY50
          stretch: 50, //slideslide 0
          depth: 100, //slidez 100
          modifier: 1, //depthrotatestretch            depth*modifierrotate*modifierstretch*modifier1
          // slideShadows: false, //slide true
        },
        navigation: {
          nextElRef: ".swiper-button-next",
          prevElRef: ".swiper-button-prev",
        },
        pagination: {
          clickable: true,
        }
      }
    }
  },
  setup() {
    // swiperswiper_options
    return {
      modules: [Pagination],
    }
  }
}
</script>

